@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@700&family=Manrope:wght@400;500;600;700&family=Readex+Pro&family=Roboto:wght@400;700&display=swap);
*{box-sizing:border-box}body,h1,li,ul,p{margin:0}li{list-style:none}body{background-color:#f5a0cb}.App{display:flex;width:844px;height:920px;font-family:'Manrope', sans-serif}

.user{display:flex;flex-direction:column;padding:20px;width:528px;height:148px}.user__header{justify-content:space-between}.user__block{display:flex;justify-content:space-between}.user__infoBlock{display:flex;flex-direction:column;justify-content:center;margin-left:12px}.user__verified{width:20px;height:20px}.user__title{display:flex;align-items:center}.user__info{font-size:20px;font-weight:600}.user__name{margin-left:4px}.user__img{width:64px;height:64px}.user__header{display:flex}.user__favInfo{display:flex;padding-top:4px}.user__favIcon{width:16px;height:16px}.user__favText{margin-left:4px;font-size:14px;color:#8398a2}.user__extraButton{all:unset;cursor:pointer}.user__gallery{padding:16px 20px;height:48px;background-color:blanchedalmond}

.message__content{margin-top:8px}.message__bubble{display:inline-block;background-color:#fff;border-radius:25px 25px 25px 4px}.message__text{padding:12px 16px;font-size:14px;line-height:140%;color:#1a1d1e}.message__date{font-size:10px;line-height:140%;color:#8398a2}.message--myMessage{display:flex;justify-content:flex-end}.message--myMessage .message__bubble{background:linear-gradient(0deg, rgba(62,181,241,0.24) -82.4%, rgba(70,203,198,0.24) 170.95%);border-radius:25px 25px 4px 25px}.message--myMessage .message__content{display:flex;flex-direction:column;align-items:flex-end}.message--myMessage .message__date{position:relative}.message--myMessage .message__checkedImg{display:inline-block;position:absolute;top:2px;left:-16px;width:14px}.message__notReadedImg{display:inline-block;position:absolute;top:2px;left:-16px;width:14px}

.messageList{margin:20px;border-top:1px solid black}

.userChat{display:flex;flex-direction:column;background-color:#ecedef;border-radius:0 30px 30px 0}

.chatFilter{display:flex;flex-direction:column;justify-content:center;align-items:center;height:83px}.chatFilter__select{position:relative;width:275px;height:44px;border-radius:14px}.chatFilter__select::after{position:absolute;content:''}

.favUsers{display:flex;justify-content:center;align-items:center;height:72px;background:linear-gradient(0deg, rgba(62,181,241,0.1) -82.4%, rgba(70,203,198,0.1) 170.95%)}.favUsers__title{font-size:16px;font-weight:600;color:#000}.favUsers__text{font-size:14px;line-height:140%;color:#8398a2}

.chatItem{display:flex;align-items:center;height:72px}.userAvatar{padding:0 12px 0 20px}.userAvatar__img{width:48px}.chatInfo__userName{font-size:16px;font-weight:600}.chatInfo__lastMessage{font-size:14px;line-height:140%;color:#8398a2}.span{color:#1a1d1e}


.chatList{width:315px;height:920px;background-color:#fff;border-radius:30px 0 0 30px}

